import React, { useState, useEffect, useRef } from 'react';
import Layout from '../../components/Layout';
import {
  Box,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  ToggleButton,
  ToggleButtonGroup,
} from '@mui/material';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import OpenInNewIcon from '@mui/icons-material/OpenInNew'; // ğŸ‘ˆ New icon import
import { SnackbarAlert } from '../../components/placeholder/SnackbarAlert';
import { PromptSection } from '../../components/placeholder/PromptSection';
import { basicHTMLApp } from '../../helpers/git';
import LoginModal from '../../components/LoginModal';
import Parse from '../../vendor/Parse';
import Editor from '@monaco-editor/react';
import Frame from 'react-frame-component';
import { useIframeMessenger } from '../../hooks/useIframeMessenger';

const AppPage = ({ location, placeholder }) => {
  const [state, setState] = useState({
    appName: 'My Web App',
    prompt: '',
    enhancedPrompt: '',
    files: {
      'index.html': basicHTMLApp,
      'style.css': '',
      'script.js': '',
    },
    snackbarOpen: false,
    snackbarMessage: '',
    snackbarSeverity: 'info',
    systemPrompt: 'create web application in a single html file, making sure UI is properly aligned and user experience is taken care of',
    showCodeEditor: true,
    showAppOutput: true,
    showAI: true,
    loginModalOpen: false,
    user: null,
    isAppDeployed: false,
    deployModalOpen: false,
    isDeploying: false,
    deployStatusMessage: '',
    isPromoting: false,
    promoteModalOpen: false,
    src: placeholder,
    dest: '',
    token: '',
    previewMode: 'live',
  });

  const [frameLoaded, setFrameLoaded] = useState(false);
  const [previewUrl, setPreviewUrl] = useState(`https://build.appseek.ai/${placeholder}`);
  const [previewUrlInput, setPreviewUrlInput] = useState(`https://build.appseek.ai/${placeholder}`);
  const [bookmarkModalOpen, setBookmarkModalOpen] = useState(false);
  const [bookmarkLabel, setBookmarkLabel] = useState('');

  const {
    appName,
    prompt,
    enhancedPrompt,
    files,
    snackbarOpen,
    snackbarMessage,
    snackbarSeverity,
    systemPrompt,
    loginModalOpen,
    user,
    isAppDeployed,
    deployModalOpen,
    isDeploying,
    deployStatusMessage,
    src,
    token,
    previewMode,
  } = state;

  const frameRef = useRef(null);

  const handleChildMessage = (message) => {
    console.log('Received from child:', message);
    if (message.type === 'deeplink' && typeof message.data === 'object') {
      const params = new URLSearchParams(message.data).toString();
      const newUrl = `${previewUrl.split('?')[0]}?${params}`;
      setPreviewUrlInput(newUrl);
      setPreviewUrl(newUrl);
    }
  };

  const { iframeRef, sendMessage } = useIframeMessenger({
    onMessageReceived: handleChildMessage,
    targetOrigin: typeof window !== 'undefined' && window.location.origin,
  });

  const queryParams = new URLSearchParams(location.search);
  const urlPrompt = queryParams.get('prompt');

  const fetchAppData = async () => {
    try {
      const result = await Parse.Cloud.run('getApp', { id: placeholder });
      const normalizedFiles = {
        'index.html': result.files['index.html'] || basicHTMLApp,
        'style.css': result.files['style.css'] || '',
        'script.js': result.files['script.js'] || '',
      };
      setState((prev) => ({
        ...prev,
        files: normalizedFiles,
        isAppDeployed: !!result?.files,
        appName: result?.appName || 'My Web App',
      }));
    } catch (error) {
      console.error('Error fetching app data:', error);
      setState((prev) => ({
        ...prev,
        isAppDeployed: false,
        files: { 'index.html': basicHTMLApp, 'style.css': '', 'script.js': '' },
        snackbarMessage: 'Failed to load app data',
        snackbarSeverity: 'error',
        snackbarOpen: true,
      }));
    }
  };

  useEffect(() => {
    fetchAppData();
  }, [placeholder]);

  useEffect(() => {
    const combinedCode = Object.values(files).join('\n\n');
    setState((prev) => ({
      ...prev,
      enhancedPrompt: `${combinedCode}\n\n${prompt}\n\n${systemPrompt}`,
    }));
  }, [files, prompt, systemPrompt]);

  useEffect(() => {
    const currentUser = Parse.User.current();
    if (currentUser) setState((prev) => ({ ...prev, user: currentUser }));
  }, []);

  useEffect(() => {
    if (urlPrompt) {
      setState((prev) => ({ ...prev, systemPrompt: urlPrompt }));
    }
  }, [urlPrompt]);

  const handleCopyPrompt = () => {
    navigator.clipboard.writeText(enhancedPrompt).then(() => {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Prompt copied to clipboard!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
      }));
    }).catch(() => {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to copy prompt',
        snackbarSeverity: 'error',
        snackbarOpen: true,
      }));
    });
  };

  const handleDeploy = async () => {
    try {
      await Parse.Cloud.run('deployApp', { files, placeholder, appname: appName.trim() });
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'App deployed successfully!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
        isAppDeployed: true,
        deployStatusMessage: 'A link will appear beside the app name',
        isDeploying: false,
      }));
      setTimeout(() => setState((prev) => ({ ...prev, deployStatusMessage: '' })), 3000);
    } catch {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to deploy app. Please try again.',
        snackbarSeverity: 'error',
        snackbarOpen: true,
        isDeploying: false,
      }));
    }
  };

  const handleFilesChange = (filePath, newCode) => {
    setState((prev) => ({
      ...prev,
      files: { ...prev.files, [filePath]: newCode },
    }));
  };

  const handleBookmarkSave = () => {
    const newBookmark = { label: bookmarkLabel.trim(), url: previewUrlInput.trim() };
    const bookmarks = JSON.parse(localStorage.getItem('bookmarks') || '[]');
    const exists = bookmarks.some(b => b.url === newBookmark.url);

    if (!exists) {
      const updated = [...bookmarks, newBookmark];
      localStorage.setItem('bookmarks', JSON.stringify(updated));
      setState(prev => ({
        ...prev,
        snackbarMessage: 'Bookmark saved!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
      }));
    } else {
      setState(prev => ({
        ...prev,
        snackbarMessage: 'Bookmark already exists!',
        snackbarSeverity: 'warning',
        snackbarOpen: true,
      }));
    }

    setBookmarkModalOpen(false);
    setBookmarkLabel('');
  };

  const generateIframeContent = () => {
    const { 'index.html': html = '', 'style.css': css = '', 'script.js': js = '' } = files;
    return `<!DOCTYPE html><html><head><style>${css}</style></head><body>${html}<script>${js}</script></body></html>`;
  };

  return (
    <Layout deployButton={
      <Box sx={{ display: 'flex', gap: 1 }}>
        <Button variant="contained" onClick={() => setState(prev => ({ ...prev, deployModalOpen: true }))}>
          Deploy
        </Button>
        <Button variant="contained" color="secondary" onClick={() => {
          Parse.Cloud.run("publishApp", { placeholder: src });
          setState(prev => ({
            ...prev,
            snackbarMessage: 'Publish functionality coming soon!',
            snackbarSeverity: 'info',
            snackbarOpen: true
          }));
        }}>
          Publish
        </Button>
      </Box>
    }>
      <Typography variant="h5" sx={{ mt: 2, mb: 3, fontWeight: 600 }}>{appName}</Typography>

      <ToggleButtonGroup
        value={previewMode}
        exclusive
        onChange={(e, val) => val && setState((prev) => ({ ...prev, previewMode: val }))}
        sx={{ mb: 2 }}
      >
        <ToggleButton value="live">Live Preview</ToggleButton>
      </ToggleButtonGroup>

      <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 2, mb: 4, height: 'calc(100vh - 250px)' }}>
        <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', border: '1px solid #ccc' }}>
          <Editor
            height="100%"
            width="100%"
            theme="vs-light"
            defaultLanguage="html"
            value={files['index.html']}
            onChange={(value) => handleFilesChange('index.html', value)}
            options={{ minimap: { enabled: false }, fontSize: 14 }}
          />
        </Box>
        <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', border: '1px solid #ccc' }}>
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', p: 1, backgroundColor: '#f5f5f5' }}>
            <TextField
              fullWidth
              variant="outlined"
              size="small"
              value={previewUrlInput}
              onChange={(e) => setPreviewUrlInput(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && setPreviewUrl(previewUrlInput)}
            />
            <Button variant="contained" onClick={() => setBookmarkModalOpen(true)}>
              <BookmarkIcon />
            </Button>
            <Button
              variant="outlined"
              onClick={() => window.open(previewUrlInput, '_blank')}
              sx={{ minWidth: '40px', px: 1 }}
            >
              <OpenInNewIcon />
            </Button>
          </Box>
          <Frame
            ref={frameRef}
            style={{ width: '100%', height: '100%', border: 'none' }}
            initialContent={generateIframeContent()}
            onLoad={() => setFrameLoaded(true)}
            mountTarget="#mount-target"
          >
            {frameLoaded && <div id="mount-target" />}
          </Frame>
        </Box>
      </Box>

      <PromptSection
        prompt={prompt}
        setPrompt={(value) => setState((prev) => ({ ...prev, prompt: value }))}
        enhancedPrompt={enhancedPrompt}
        handleCopyPrompt={handleCopyPrompt}
        systemPrompt={systemPrompt}
        setSystemPrompt={(value) => setState((prev) => ({ ...prev, systemPrompt: value }))}
      />

      <LoginModal
        open={loginModalOpen}
        onClose={() => setState((prev) => ({ ...prev, loginModalOpen: false }))}
        onLoginSuccess={() => {
          const currentUser = Parse.User.current();
          setState((prev) => ({ ...prev, user: currentUser, loginModalOpen: false }));
        }}
      />

      <Dialog open={deployModalOpen} onClose={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>
        <DialogTitle>Bookmark This App</DialogTitle>
        <DialogContent>
          <DialogContentText>Before deploying, we recommend bookmarking this app to avoid losing it.</DialogContentText>
          <TextField
            autoFocus
            fullWidth
            label="App Name"
            value={state.appName}
            onChange={(e) => setState((prev) => ({ ...prev, appName: e.target.value }))}
            sx={{ mt: 1 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>Cancel</Button>
          <Button
            onClick={() => { setState((prev) => ({ ...prev, isDeploying: true, deployModalOpen: false })); handleDeploy(); }}
            color="primary"
            variant="contained"
            disabled={isDeploying}
          >
            {isDeploying ? 'Deploying...' : 'Deploy'}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={bookmarkModalOpen} onClose={() => setBookmarkModalOpen(false)}>
        <DialogTitle>Save Bookmark</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            fullWidth
            label="Label"
            value={bookmarkLabel}
            onChange={(e) => setBookmarkLabel(e.target.value)}
            sx={{ mt: 1 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setBookmarkModalOpen(false)}>Cancel</Button>
          <Button onClick={handleBookmarkSave} color="primary" variant="contained">
            Save
          </Button>
        </DialogActions>
      </Dialog>

      <SnackbarAlert
        open={snackbarOpen}
        message={snackbarMessage}
        severity={snackbarSeverity}
        onClose={() => setState((prev) => ({ ...prev, snackbarOpen: false }))}
      />
    </Layout>
  );
};

export default AppPage;
